












import Vue from 'vue';
export default Vue.extend({
  name: 'z-card',
  props: {
    title: String,
    collapsed: {
      type: Boolean,
      default: false,
    },
    hasCollapse: {
      type: Boolean,
      default: true,
    },
    hasClose: {
      type: Boolean,
      default: false,
    },
  },
  mounted() {
    this.isCollapsed = this.collapsed;
  },
  data() {
    return {
      isCollapsed: false,
    };
  },
  computed: {
    fontName(): string {
      return !this.isCollapsed ? 'minus-circle' : 'plus-circle';
    },
    titleClass(): Record<string, boolean> {
      return {
        'text-gray-800': !this.isCollapsed,
        'text-gray-600 cursor-pointer': this.isCollapsed,
      };
    },
  },
  methods: {
    handleCollapse() {
      this.isCollapsed = !this.isCollapsed;
    },
    showBody() {
      if (this.isCollapsed) {
        this.handleCollapse();
      }
    },
  },
});
